}

input[type="range"] {
  appearance: none;
  cursor: pointer;
  width: 15rem;
  background: none;
  align-self: center;
}

input[type="range"]::-webkit-slider-runnable-track {
  height: .5rem;
  background: #000;
  border-radius: .5rem;
}

input[type="range"]::-webkit-slider-thumb {
  appearance: none;
  height: 1.5rem;
  max-width: 1.5rem;
  background-color: #f5f5f5;
  border: 4px solid #000;
  border-radius: 50%;
