input[type="checkbox"] {
  -webkit-appearance: none;
}

.setting__checkbox {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background: #000;
  border-radius: 20px;
  margin: 0 .5rem;
  transition: all .5s ease-in;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.setting__checkbox:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #f5f5f5;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 2px;
  left: 2px;
}

input[type="checkbox"]:checked ~ .setting__checkbox {
  background: #f5f5f5;
}

input[type="checkbox"]:checked ~ .setting__checkbox:before {
  transform: translateX(-50px);
}

.setting__checkbox:after {
  content: "";
  width: 16px;
  height: 16px;
  background: #000;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 2px;
  left: 2px;
  transform: translateX(50px);
}

input[type="checkbox"]:checked ~ .setting__checkbox:after {
  transform: translateX(20px);
}

